import React, { Component } from 'react';

export default class BackTopComponent extends Component {
	componentDidMount() {
		const scrollBtn = document.getElementById('backTop');
		window.addEventListener('scroll', scrollFunc, false);
		scrollBtn.addEventListener('click', moveFunc, false);
		function scrollFunc() {
			let scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
			if (scrollTop > 100) {
				scrollBtn.style.display = 'block';
			} else {
				scrollBtn.style.display = 'none';
			}
		}
		function moveFunc(number = 0, time) {
			if (!time) {
				document.body.scrollTop = document.documentElement.scrollTop = number;
				return number;
			}
			const spacingTime = 20; // è®¾ç½®å¾ªç¯çš„é—´éš”æ—¶é—´  å€¼è¶Šå°æ¶ˆè€—æ€§èƒ½è¶Šé«˜
			let spacingInex = time / spacingTime; // è®¡ç®—å¾ªç¯çš„æ¬¡æ•°
			let nowTop = document.body.scrollTop + document.documentElement.scrollTop; // è·å–å½“å‰æ»šåŠ¨æ¡ä½ç½®
			let everTop = (number - nowTop) / spacingInex; // è®¡ç®—æ¯æ¬¡æ»‘åŠ¨çš„è·ç¦»
			let scrollTimer = setInterval(() => {
				if (spacingInex > 0) {
					spacingInex--;
					moveFunc((nowTop += everTop));
				} else {
					clearInterval(scrollTimer); // æ¸…é™¤è®¡æ—¶å™¨
				}
			}, spacingTime);
		}
	}
	render() {
		return (
			<div className="back-top" id="backTop">
				<i className="icon iconfont lphva-icon-fanhuidingbu">î˜œ</i>
			</div>
		);
	}
}
